
import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import { Message } from 'element-ui'
Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    component: () => import('@/views/LoginView')
  },
  {
    path: '/',
    component: () => import('@/views/LayoutContains'),
    redirect: '/home',
    children: [
      {
        path: '/home',
        component: () => import('@/views/home/HomeIndex')
      },
      {
        path: '/task/business',
        component: () => import('@/views/task/TaskBusiness')
      },
      {
        path: '/task/operation',
        component: () => import('@/views/task/TaskOperation.vue')
      },
      {
        path: '/node/region',
        component: () => import('@/views/node/RegionPage.vue')
      },
      {
        path: '/node/node',
        component: () => import('@/views/node/NodeManagement.vue')
      },
      {
        path: '/node/partner',
        component: () => import('@/views/node/NodePartner.vue')
      },
      {
        path: '/vm/index',
        component: () => import('@/views/vm/VmManagement.vue')
      },
      {
        path: '/vm/status',
        component: () => import('@/views/vm/VmStatus.vue')
      },
      {
        path: '/vm/type',
        component: () => import('@/views/vm/VmType.vue')
      },
      {
        path: '/user/index',
        component: () => import('@/views/user/UserList.vue')
      },
      {
        path: '/user/user-task-stats',
        component: () => import('@/views/user/UserStatistics.vue')
      },
      {
        path: '/user/user-work',
        component: () => import('@/views/user/UserWork.vue')
      },
      {
        path: '/sku/sku-class',
        component: () => import('@/views/sku/SkuType.vue')
      },
      {
        path: '/sku/sku',
        component: () => import('@/views/sku/SkuManagement.vue')
      },
      {
        path: '/policy/index',
        component: () => import('@/views/policy/PolicyManagement.vue')
      },
      {
        path: '/order/index',
        component: () => import('@/views/order/OrderManagement.vue')
      },
      {
        path: '/report/index',
        component: () => import('@/views/report/ReportStatistics.vue')
      }
    ]
  }
]

const router = new VueRouter({
  routes
})

const whiteList = ['/login']
router.beforeEach(async (to, from, next) => {
  const token = store.state.user.token
  // console.log(token)
  if (token) {
    if (to.path === '/login') {
      next('/')
    } else {
      // 有token但是没有用户信息 就获取用户信息 如果有就不需要再获取
      if (!store.state.user.userInfo.userId) {
        console.log(store)
        await store.dispatch('user/getUserInfo')
      }
      next()
    }
  } else {
    if (whiteList.includes(to.path)) {
      next()
    } else {
      next('/login')
      Message.error('请登录')
    }
  }
})
export default router
