import moment from 'moment'
export default {
  // 计算当前时间和当前月的第一天 用于参数进行传参
  time: () => {
    const end = moment().locale('zh-cn').format('YYYY-MM-DD')
    const start = moment(end).startOf('month').format('YYYY-MM-DD')
    return { start, end }
  },
  times: () => {
    const end = moment().locale('zh-cn').format('YYYY-MM-DD HH:mm:ss')
    const start = moment(end).startOf('month').format('YYYY-MM-DD 00:00:00')
    return { start, end }
  },
  // 计算每周第一天和当前日期
  week: () => {
    const end = moment().locale('zh-cn').format('YYYY-MM-DD')
    const start = moment(end).startOf('week').add(1, 'day').format('YYYY-MM-DD')
    return { start, end }
  },
  // 计算每年第一月第一天和当前日期
  year: () => {
    const end = moment().locale('zh-cn').format('YYYY-MM-DD')
    const start = moment(end).startOf('year').format('YYYY-01-01')
    return { start, end }
  }
}
